"use client";
import { Geist, Geist_Mono } from "next/font/google";
import "./globals.css";
import Sidebar from "../components/Sidebar";
import { Toaster } from "react-hot-toast";
import { usePathname } from "next/navigation";

const geistSans = Geist({
  variable: "--font-geist-sans",
  subsets: ["latin"],
});

const geistMono = Geist_Mono({
  variable: "--font-geist-mono",
  subsets: ["latin"],
});

export default function RootLayout({ children }) {
  const pathname = usePathname();

  // Routes where sidebar should be shown
  const sidebarRoutes = [
    "/orderDashboard",
    "/stocks",
    "/rawItems/manageRawItems",
    "/supplierManage",
    "/transaction",
  ];

  const shouldShowSidebar = sidebarRoutes.some((route) =>
    pathname.startsWith(route)
  );

  return (
    <html lang="en">
      <head>
        <title>Ozone Mineral Water - Sip the Good Life</title>
        <meta
          name="description"
          content="Premium mineral water available in 500ml and 1500ml bottles. Experience pure hydration with Ozone Mineral Water."
        />
      </head>
      <body
        className={`${geistSans.variable} ${geistMono.variable} antialiased`}
      >
        <Toaster position="top-right" />
        {shouldShowSidebar && <Sidebar />}
        {children}
      </body>
    </html>
  );
}
